html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }
body { line-height: 1; }
ol, ul { list-style: none; }
blockquote, q { quotes: none; }
blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }
table { border-collapse: collapse; border-spacing: 0; }


body {
	font-family: "Open Sans","Microsoft YaHei","Hiragino Sans GB","Hiragino Sans GB W3","微软雅黑","Helvetica Neue",Arial,sans-serif;
    font-size: 18px;
    line-height: 1.6em;
    color: #333;
    margin: 0;
    padding: 0;
    font-weight: 100;
    -webkit-font-smoothing: antialiased;
}

.main {
	margin: 20px 0;
}

.container {
	width: 780px;
	margin: 0 auto;
}

.basic-information {
	padding: 80px 0 40px 0;
}

.basic-information p {
	margin: 0 4rem;
	margin-bottom: 1.4em;
}

h1 { 
	text-transform: uppercase; 

	font-size: 48px;
    line-height: .8em;
    letter-spacing: -.08em;
    font-weight: 900;
    text-transform: uppercase;

    text-align: center;
    margin: 40px auto;
	width: 60%;
  	border-top: 1px solid #999;
    border-bottom: 1px solid #999;
    padding: 20px 0;
}

h2 {
	font-size: 26px;
	font-weight: 600;
	font-style: italic;
	text-align: center;
    line-height: 1.4em;

	padding: 40px 0;
	margin: 40px 0;
}

.tagline {
	font-size: 24px;
	font-weight: bold;
	margin-bottom: 1.4em;
	text-transform: capitalize;
}

.block {
	margin: 4rem 4rem;
	font-size: 16px;
}

.skillset .category {
	margin: 1rem 0;
}

.skillset .category .name{
	font-weight: 600;
	font-style: italic;
}

.project {
	margin: 2.6rem 0;
	line-height: 1.4rem;
}

.project h4 {
	color: #999;
	font-weight: 400;
	font-size: 18px;
}

.project .role {
	margin-bottom: 1.2rem;
	font-weight: 600;
}

p.description {
	line-height: 1.6rem;
	margin-bottom: 1rem;
}

.button {
	padding: 6px 0;
	background: #20A95B;
	color: white;
	border-radius: 2px;
	text-decoration: none;
	display: inline-block;
	text-align: center;
	width: 100%;
	font-size: 20px;
    font-weight: 200;
    border: 1px solid #20a958;
    font-family: inherit;
}

.button:hover {
	opacity: .9;
}

.resume-form input, textarea {
	width: 100%;
	padding: 10px 16px;
	border: 1px solid #999;
	font-size: 16px;
	border-radius: 2px;
	color: #333;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.resume-form button {
	padding: 10px 20px;
	background: #20A95B;
	color: white;
	border-radius: 4px;
	text-decoration: none;
	font-size: 16px;
	border: 1px solid #20A95B;
}

.buttons {
	margin: 20px 0;
}

.resume-form button[disabled] {
	opacity: .5;
}

.resume-form input[type=checkbox] {
	width: auto;
}

.resume-form .form-item {
	margin: 10px 0;
}

.resume-item {
	padding: 10px 20px;
	border-radius: 2px;
	background-color: white;
	border: 1px solid #999;
	margin: 10px 0;
}

.resume-item a {
	text-decoration: underline;
	color: #333;
}

.button-container {
	margin: 20px 0;
}

h3.title {
	font-weight: 600;
}

h3.page-title {
	font-weight: 700;
	font-size: 24px;
	padding: 20px 0;
	text-transform: capitalize;
}

.pull-right {
	float: right;
}

button.reset {
	margin-left: 10px;
	background-color: white;
	color: #20A95B;
}

.inline-form {
	display: -ms-flexbox;
	display: flex;
}

.inline-form-input {
	-ms-flex: 4 1;
	    flex: 4 1;
	margin-right: 10px;
}

.inline-form-input input[type="text"] {
	width: 100%;
	padding: 10px 16px;
	border: 1px solid #999;
	font-size: 16px;
	border-radius: 2px;
	color: #333;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.inline-form-action {
	-ms-flex: 1 1;
	    flex: 1 1;
}


